<template>
    <div>
        <div class="container">
            <el-form ref="filters" :inline="true" :model="filters" class="toolbar">
                <el-form-item label="姓名" :inline="true">
                    <el-input v-model.trim="filters.movieName" placeholder="请输入姓名"></el-input>
                </el-form-item>
                <el-form-item label="联系人电话" :inline="true">
                    <el-input v-model.trim="filters.movieName" placeholder="请输入车牌号"></el-input>
                </el-form-item>
                
                <el-button>查询</el-button>
            </el-form>
            <el-table :data="tableData" border class="table" ref="multipleTable" @selection-change="handleSelectionChange">
                <el-table-column prop="borrower" label="姓名" width="120"></el-table-column>
                <el-table-column prop="contact_way" label="联系电话" width="120"></el-table-column>
                <el-table-column prop="id_card" label="身份证号" width="120"></el-table-column>
                <el-table-column prop="address" label="家庭住址" width="120"></el-table-column>
                <el-table-column prop="bank_card_number" label="卡号" width="120"></el-table-column>
                <el-table-column prop="open_bank" label="开户行" width="120"></el-table-column>
                <el-table-column prop="total_loan_amount" label="总贷款金额（元）" width="120"></el-table-column>
                <el-table-column prop="bank_profit" label="银行贷款金额（元）" width="120"></el-table-column>
                <el-table-column prop="ew_loan_amount" label="二网贷款金额（元）" width="120"></el-table-column>
                <el-table-column prop="loan_staging" label="贷款期数" width="120"></el-table-column>
                <el-table-column prop="pay_count" label="已还期数" width="120"></el-table-column>
                <el-table-column prop="bx_already_pay" label="本息待还款金额（元）" width="120"></el-table-column>
                <el-table-column prop="no_payment_day" label="欠款天数" width="120"></el-table-column>
                <el-table-column label="操作" width="180" align="center" fixed="right">
                    <template slot-scope="{row, $index}">
                        <el-button type="text" icon="el-icon-view" @click="$router.push({path: '/creditHelpManagement/view', query: {id:row.id, credit_type: row.credit_type, page_type: true}})">查看详情</el-button>
                        <el-button type="text" icon="el-icon-delete" @click="" disabled>操作记录</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination background @current-change="handleCurrentChange" layout="prev, pager, next" :total="1000">
                </el-pagination>
            </div>
        </div>
    
    </div>
</template>

<script>
    import * as API from '@/api/creditManagement'
    export default {
        name: 'basetable',
        data() {
            return {
                tableData: [],
                page: 1,
                filters: {
                    movieID:'',
                    movieName:'',
                    value:'3'
                }
            }
        },
        mounted() {
            this.getData();
        },
        computed: {
            
        },
        methods: {
            // 分页导航
            handleCurrentChange(val) {
                this.page = val;
                this.getData();
            },
            getData() {
                
                API.creditManagementBlackList({page: this.page}).then(res => {
                    this.tableData = res.data;
                })
               
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            }
        }
    }

</script>

<style scoped>
    .handle-box {
        margin-bottom: 20px;
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .del-dialog-cnt{
        font-size: 16px;
        text-align: center
    }
    .table{
        width: 100%;
        font-size: 14px;
    }
    .red{
        color: #ff0000;
    }
</style>
